<template>
    <div class="container">
        <div class="list-container">
            <div class="list-item" v-for="(item,index) in tasks" :key="index" @click="taskTap">
                <div class="img">
                    <img :src="item.cover" />
                </div>
                <div class="info">
                    <div class="title">
                        <span>{{item.title}}</span>
                        <span>+{{ item.price }}</span>
                    </div>
                    <div class="type">
                        <span>{{item.type}}</span>
                    </div>
                    <div class="dose">
                        <span>已接{{item.dose}}</span>
                        <span>开始</span>
                    </div>
                </div>
            </div>
        </div>
        <g-dialog :show="showDialog" title="确定清空任务数据?" @cancel="cancel" @confirm="confirm"/>
    </div>
</template>
<script>
import { mapGetters,mapActions } from 'vuex'
export default{
    data(){
        return{
            showDialog:false,
        }
    },
    computed: {
        ...mapGetters(['tasks']),
    },
    methods:{
        ...mapActions(['CLEAR_TASK_ASYNC']),
        taskTap(){
            this.showDialog = true
        },
        cancel(){
            this.showDialog = false
        },
        confirm(){
            this.showDialog = false
            this.CLEAR_TASK_ASYNC()
        }
    }
}
</script>
<style scoped>
.container{
    margin: 0;
    padding: 0;
    background-color: #f3f1f1;
    min-height: 100vh;
}
.list-container{
    display: flex;
    flex-direction: column;
    padding: 8px 15px;
}
.list-item{
    border-radius: 6px;
    overflow: hidden;
    display: flex;
    align-items: center;
    background-color: #fff;
    margin-bottom: 6px;
    padding: 6px;
}
.list-item>.img{
    height: 80px;
    width: 120px;
    object-fit: contain;
    border-radius: 6px;
    overflow: hidden;
}
.list-item>.img>img{
    width: 100%;
    height: 100%;
}
.list-item>.info{
    padding: 5px;
    flex: 1;
}
.list-item>.info>.title{
    display: flex;
    justify-content: space-between;
    font-size: 17px;
}
.list-item>.info>.title>span:last-child{
    color: #ec602d;
}
.list-item>.info>.type{
    display: flex;
    margin-top: 2px;
}
.list-item>.info>.type>span{
    display: block;
    background-color: #f5f5f5;
    margin-right: -8px;
    font-size: 12px;
    padding: 4px 6px;
    border-radius: 4px;
    -webkit-transform: scale(.8);
    transform: scale(.8);
    -webkit-transform-origin: 0;
    transform-origin: 0;
}

.list-item>.info>.dose{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #999;
    font-size: 14px;
    margin-top: 1px;
}
.list-item>.info>.dose>span:last-child{
    background-color: #ec602d;
    color: #fff;
    padding: 2px 6px;
    border-radius: 4px;
}
</style>